      outline: none;
      border: none;
      color: #a7a7a7;
      background-color: #3c3c3c;
      resize: none;
      height: 300px;
      width: 200px;
    }

    label h1 {
      font-weight: %125;
      color: #a7a7a7;
      text-align: left;
      font-size: 10pt;
    }

