      margin-right: 10px;
    }

    #head {
      font-size: 250%;
      color: #8c50cf;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    #info {
      font-size: 75%;
      color: #a7a7a7;
      margin-bottom: 40px;
    }
}
